.star-border-container {
    display: inline-block;
    padding: 1px 0;
    position: relative;
    border-radius: 20px;
    overflow: hidden;
  }
  
  .border-gradient-bottom {
    position: absolute;
    width: 300%;
    height: 50%;
    opacity: 0.7;
    bottom: -11px;
    right: -250%;
    border-radius: 50%;
    animation: star-movement-bottom linear infinite alternate;
    z-index: 0;
  }
  
  .border-gradient-top {
    position: absolute;
    opacity: 0.7;
    width: 300%;
    height: 50%;
    top: -10px;
    left: -250%;
    border-radius: 50%;
    animation: star-movement-top linear infinite alternate;
    z-index: 0;
  }
  
  .inner-content {
    position: relative;
    background: rgba(23, 25, 30, 0.6); /* make background semi-transparent */
    border: 1px solid #222;
    color: white;
    font-size: 16px;
    text-align: center;
    padding: 16px 26px;
    border-radius: 20px;
    z-index: 1;
    backdrop-filter: blur(20px); /* <- the real blur magic */
    -webkit-backdrop-filter: blur(20px); /* for Safari */
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.7); /* optional soft shadow around */
  }
  
  @keyframes star-movement-bottom {
    0% {
      transform: translate(0%, 0%);
      opacity: 1;
    }
    100% {
      transform: translate(-100%, 0%);
      opacity: 0;
    }
  }
  
  @keyframes star-movement-top {
    0% {
      transform: translate(0%, 0%);
      opacity: 1;
    }
    100% {
      transform: translate(100%, 0%);
      opacity: 0;
    }
  }
  